*,*::before,*::after{margin:0;padding:0;box-sizing:inherit}html{box-sizing:border-box;font-size:62.5%}body{font-family:"Comfortaa", cursive;font-size:1.6rem;line-height:1.6;position:relative;background-color:#f1f2f6}.container{width:80%;margin:0 auto;min-height:70vh;background-color:#f1f2f6;display:grid;grid-template-columns:1fr 20vw;grid-gap:1rem}.about{grid-column:1;background-color:#fff;min-height:50vh;display:flex;flex-direction:column}.about__item{margin:4rem;padding:2rem;border-radius:10px;background-color:#ffeaa7;display:flex;flex-direction:column;align-items:center;text-align:center}.about__item__title{font-size:3rem;letter-spacing:0.5rem;margin:2rem}.about__item__text{width:75%}.content{grid-column:1;background-color:#fff;min-height:50vh;display:flex;flex-direction:column}.content__title{padding:2rem;text-align:center;background-color:#ffeaa7}.content__item{margin:4rem;padding:2rem;border-radius:10px;background-color:#ffeaa7;display:grid;grid-template-columns:10rem 1fr;align-items:center}.content__item:not(:last-child){margin-bottom:0}.content__item__img{width:7rem;height:7rem;margin-right:2rem;border-radius:10px}.content__item a{text-decoration:none}.content__item__title{font-size:2rem;letter-spacing:0.5rem;color:black}.content__item__text{grid-column:2}.detail{grid-column:1;background-color:#fff;min-height:50vh;display:flex;flex-direction:column}.detail__item{margin:4rem;padding:2rem;border-radius:10px;background-color:#ffeaa7;display:flex;flex-direction:column;align-items:center;text-align:center}.detail__item__img{margin-top:2rem;width:80%;height:20rem;object-fit:cover;border-radius:10px}.detail__item__title{font-size:3rem;letter-spacing:0.5rem;margin:2rem}.detail__item__text{width:75%}.footer{grid-column:1/-1;background-color:#ffeaa7;font-size:2rem;height:15vh;display:flex;align-items:center;justify-content:center}.header{grid-column:1/-1;background-image:linear-gradient(to right, rgba(241,242,246,0.2), rgba(241,242,246,0.2)),url(/static/media/header.9